<template>
   <div class="BoxContactUs">
      <v-snackbar
         v-model="snackbar"
         :color="NotifColor"
         :timeout="timeout"
         top
         style="margin:auto;"
         >
            <p style="margin-bottom:0px;text-align:center;width: 100%;">{{NotifText}}</p>
      </v-snackbar>
      <div class="BoxContactUs__BxHeader">
         <div class="BoxContactUs__BxHeader--BxLogo">
             <img src="https://liveb2itest.id.dyned.com/assets/images/logo.png">
         </div>
         <div class="BoxContactUs__BxHeader--BxNav">
            <router-link class="BLogin" to="/login">{{ $t('login') }}</router-link>
            <router-link class="BContact bxactive" to="/contact">{{ $t('contactUs') }}<div></div></router-link>
            <router-link class="BAbout" to="/about">{{ $t('aboutUs') }}</router-link>
         </div>
      </div>
      <div class="BoxContactUs__BxContact">
         <div class="BoxContactUs__BxContact--BxLotte">
            <PlanetCommetAstronotLayangan />
         </div>
         <div class="BoxContactUs__BxContact--BxConc">
            <div class="bxHeading">
              {{ $t('contactTitle') }}
            </div>

            <div class="BxForm">
               <div class="BxForm__SelectQ">
                  <!-- <v-select
                     :items="items"
                     solo
                     outlined
                     label="Frequently Asked Questions"
                  ></v-select> -->
               </div>
               <div class="BxForm__TextArea">
                  <v-textarea
                     solo
                     name="input-7-4"
                     rows="14"
                  ></v-textarea>
               </div>
                <div class="BxForm__Vbtn">
                   <v-btn @click="SubmitTicket" outlined> {{ $t('contactBtn') }}</v-btn>
                </div>
            </div>
         </div>
      </div>
      <v-dialog v-model="dialogLogin" content-class="DialogLoder" width="300" hide-overlay persistent style="display:flex;justify-content:center;">
         <v-card class="fpassword--loder">
            <v-card-text class="text-xs-center">
               <Littleloading  style="height:250px;width:250px;overflow:hidden;"/>
            </v-card-text>
         </v-card>
      </v-dialog>
   </div>
</template>

<script>
   import PlanetCommetAstronotLayangan from '../Lotte/PlanetCommetAstronotLayangan'
   import Littleloading from '../Lotte/LittleLoading'
   import i18n from '../../plugins/i18n';	
   export default {
      components:{
         PlanetCommetAstronotLayangan,
         Littleloading
      },
      data() {
         return{
            items: ['Frequently Asked Questions'],
            dialogLogin:false,
            timeout:6000,
            NotifColor:'success',
            snackbar:false,
            NotifText:'Iam Alert'
         }
      },
      methods: {
         SubmitTicket(){
            this.dialogLogin = true

            setTimeout(() => {
               this.dialogLogin = false
               this.snackbar = true
            }, 2000);
         }
      },

   }
</script>

<style lang="scss">
     @import './ContactUs.scss';
</style>


